 //æ€Žä¹ˆç”Ÿæˆ10000æ¡æ•°æ®ï¼Ÿ  Array.from  ç”¨å†…å­˜æ¥æ”¾ï¼Œæ‰10000æ¡ï¼Œ
        //å’Œä¸€ä¸ª4kyuçš„é¢˜ç›®å¾ˆåƒï¼Œåˆ†é¡µ originNews åˆ†æˆ200ä¸ªæ•°ç»„ï¼ˆäºŒç»´ï¼‰ [[50][50]]
       // 10000 æ¡æ•°æ®ï¼Ÿ  Array.from  å†…å­˜
  // 4yu  10000 originNews 200ä¸ªæ•°ç»„ [[50],[50]]
  let page = 0;  // é¡µç ,è¿™é‡Œæ˜¯æ¯é¡µå›ºå®šæ•°é‡ï¼Œæœ‰æ ·å¼ï¼Œè§¦åº•åˆ·å‡ºä¸‹ä¸€é¡µ
  let pageNum = Math.ceil(window.innerHeight / 50);
  const chunk = (arr, size) => Array.from({
    length: Math.ceil(arr.length / size)
  }, (v, i) => 
    arr.slice(i * size, i * size + size)
  )
    
  const originNews = Array.from({
    length: 1000
  }, (v, k) => ({
    content: `æ–°é—»${k}`
  }))
  let news = chunk(originNews, pageNum) // å½“å‰é¡µéœ€è¦çš„æ•°æ®
  // console.log(news)

  const element = document.querySelector('.news'); // ä»£å­•çš„
  const loadObserver = new IntersectionObserver((entries) => {
    // if ()
    // console.log(entries);
    if (entries[0].intersectionRatio <= 0) {
      return;
    }
    const hasPrePage = page != 0; // page å½“å‰é¡µé¢, 
    const hasNextPage = page != news.length - 1 // æœ€åŽä¸€é¡µï¼Ÿ 
    const now = news[page]
    const pre = hasPrePage ? news[page - 1]:[];
    const next = hasNextPage ? news[page + 1]: [];

    render(pre.length, [...pre, ...now, ...next]);
    page = entries[0].target.className == 'news-footer' || page == 0
      ? (hasNextPage? page + 1: page)
      : (hasPrePage? page - 1: page)
    
  }, {
    threshold:[1]
  }) // ç›‘å¬

  loadObserver.observe(document.querySelector('.news-header'));
  loadObserver.observe(document.querySelector('.news-footer'));
 // å»ºè®®å‡½æ•°å†…çš„ä»£ç ä¸è¦è¶…è¿‡10è¡Œ å°è£…
  function render(last, data) {
    element.innerHTML = '';
    data.forEach((i, v) => element.innerHTML += v == last 
      ? `<div id="news-href">${i.content}</div>`
      :`<div>${i.content}</div>`
    )
    window.location.href = '#news-href';
  }
